@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .reasons {
        &_block {
            align-items: flex-start; }
        &_descr {
            width: 300px; } } }

@media (max-width: 991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 10px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            height: 40px;
            width: 100%; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-style: 30px; } }

    .reasons {
        &_block {
            flex-direction: column;
            align-items: center; }
        &_round {
            margin-right: 0; }
        &_descr {
            width: 300px;
            margin-top: 20px; } } }

@media (max-width: 767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0,0,0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }

        &_item {
            padding-right: 0;
            border: none;
            margin: 10px 0; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            color: fff;
            font-style: 16px;
            font-weight: 400;
            text-transform: uppercase;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        &_header {
            line-height: 45px; } } }

@media (max-width: 575px) {
    nav {
        height: 30px; }
    header {
        height: 30px; }

    .subheader {
        height: 60px;
        top: 30px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 16px; }
        &_phone {
            font-size: 12px;
            text-transform: uppercase;
            font-weight: 400;
            margin-top: 11px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 6px; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            margin-top: 33px;
            padding: 0 10px;
            line-height: 30px; }
        &_btn {
            width: 210px;
            height: 50px;
            margin-top: 72px; } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .reasons {
        padding: 22px 0 36px 0;
        &_block {
            margin-top: 26px;
            justify-content: center;
            align-items: center; }

        &_round {
            width: 100px;
            height: 100px;
            justify-content: center;
            align-items: center; }

        &_descr {
            width: 100%;
            margin-top: 27px; }
        &_subtitle {
            font-size: 16px; } }
    .title {
        font-size: 16px;
        margin-top: 15px;
        line-height: 16px; }
    .mobile {
        &_item {
            width: 290px;
            height: 254px; } }
    .text_1 {
        display: none; }
    .text_2 {
        display: none; }
    .car {
        width: 100%;
        margin-top: 35px; }

    .choice {
        min-height: 390px;
        padding: 25px 0 0 0;
        background: url('../img/fon_3.png') center (center / cover) no-repeat; }
    .text_3 {
        margin-top: 23px;
        font-size: 17px;
        color: #fff; }
    .require {
        padding: 25px 0 26px 0;
        &_block {
            margin: 22px 0; }
        &_title {
            font-size: 16px; }
        &_list {
            padding-left: 25px;
            margin-top: 11px;

            li {
                font-size: 14px;
                font-weight: 150; } }
        &_discr {
            margin-top: 10px;
            font-saze: 14px; } }
    .city {}
    height: 360px;
    .text_22 {}
    margin-top: 19px;
    margin-bottom: 180px;
    .city_link {
            margin-top: 140px; }

    footer {
        min-height: 245px; }
    .footer_wrapper {
        flex-direction: column;
        padding-top: 22px;
        justify-content: center;
        align-items: center; }
    .footer_wrapper > div {
        &:nth-child(1) {
            order: 2; }
        &:nth-child(2) {
            order: 1; }
        &:nth-child(3) {
            order: 3; } }
    .footer_links {
        width: 290px;
        &_main {
            padding: 0;
            font-size: 14px; } }
    .footer_links_sub {
            flex-wrap: wrap;
            justify-content: center;
            a {
                margin: 0 7px; } }
    .footer_links_lang {
            margin-top: 10px; }
    .footer_social {
        margin-top: 10px;
        justify-content: center;
        align-items: center; }

    .footer_mobile {
        margin-top: 10px;
        justify-content: center;
        align-items: center; } }




















