.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/fon.png') center (center / cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/img_1.png') center (center / cover) no-repeat; }
    &_2 {
      background: url('../img/img_2.png') center (center / cover) no-repeat; }
    &_3 {
      background: url('../img/img_3.png') center (center / cover) no-repeat; }
    &_4 {
      background: url('../img/img_4.png') center (center / cover) no-repeat; }
    &_5 {
      background: url('../img/img_5.png') center (center / cover) no-repeat; }
    &_6 {
      background: url('../img/img_6.png') center (center / cover) no-repeat; }
    &_subtitle {
      font-size: 24px;
      text-transform: uppercase;
      text-wrap: 400px;
      letter-spacing: normal;
      text-align: center;
      color: #fff; }
    &_plus {
      position: absolute;
      width: 26px;
      height: 26px;
      background-color: #000;
      margin: 0 auto;
      bottom: 35px;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 100%;
      padding: 6px;
      cursor: pointer;
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        display: block;
        width: 14px;
        height: 2px;
        background-color: $main_color;
        transition: 0.5s all; }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        width: 2px;
        height: 14px;
        background-color: $main_color;
        transition: 0.5s all; }
      &:hover {
        &:before, &:after {
          background-color: $second_color; } }


      img {
        display: block;
        margin: 0 auto; } } } }
